<template>
    <div class="login">
        <h1 v-html="t('pages.login.title')"></h1>

        <div class="description">
            <p>{{ t('pages.login.description.intro') }}</p>
            <ul>
                <li v-for="(item, id) in data.steps" :key="id" v-html="item"></li>
            </ul>
            <div class="code">{{ user.externalCode }}</div>
        </div>

        <div class="footer">
            <div class="terms">
                <span>
                    {{ t('pages.login.terms') }}
                    <a href="https://github.com/Bilbo-Music/bilbo-docs-public/blob/main/terms%20of%20service/Bilbo%20Studio.md" target="_blank">{{ t('pages.login.terms-button') }}</a>
                    {{ t('pages.login.terms-2') }}
                </span>
            </div>
            <Select
                class="noerror inline icon-only"
                :optionList="locale.languages"
                :value="locale.locale"
                :clearable="false"
                :leftAuto="!locale.rtl"
                :rightAuto="locale.rtl"
                @change="onLanguageChange"
            />
        </div>
    </div>
</template>

<script setup>
import { reactive, watch, onMounted, onBeforeUnmount } from 'vue';
import { useI18n } from 'vue-i18n';
import Button from 'ðŸ”—/components/ui/Button.vue';
import Select from 'ðŸ”—/components/ui/Select.vue';
import stores from '@/stores';

const user = stores.user();
const nav = stores.nav();
const locale = stores.locale();

const { t, tm, rt } = useI18n();

const data = reactive({
    steps: []
});

const getSteps = () => {
    data.steps = Array.isArray(tm('pages.login.description.steps')) ? tm('pages.login.description.steps').map(item => rt(item)
    ) : [];
}

const onLanguageChange = (payload) => {
    locale.updateLocale(payload.value);
};

const updateCode = async () => {
    await user.createExternalLogin();

    clearTimeout(data.updateCodeTimeout);
    data.updateCodeTimeout = setTimeout(() => {
        updateCode();
    }, 5 * 60 * 1000); // ÐšÐ°Ð¶Ð´Ñ‹Ðµ 5 Ð¼Ð¸Ð½ÑƒÑ‚ Ð½Ð°Ð´Ð¾ Ð°ÐºÑ‚ÑƒÐ°Ð»Ð¸Ð·Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ Ñ‚Ð¾ÐºÐµÐ½

    setTimeout(() => {
        checkCode();
    }, 5 * 1000);
}

const checkCode = async () => {
    const result = await user.checkExternalLogin();

    if (!result) {
        clearTimeout(data.checkCodeTimeout);
        data.checkCodeTimeout = setTimeout(() => {
            checkCode();
        }, 4 * 1000); // ÐšÐ°Ð¶Ð´Ñ‹Ðµ 4 ÑÐµÐºÑƒÐ½Ð´Ñ‹ Ð½Ð°Ð´Ð¾ Ð·Ð°Ð¿Ñ€Ð°ÑˆÐ¸Ð²Ð°Ñ‚ÑŒ ÑÑ‚Ð°Ñ‚ÑƒÑ
        return;
    }

    await user.getArtists();

    nav.open('/');
}


watch(() => locale.locale, () => {
    getSteps();
});

onMounted(() => {
    getSteps();
    updateCode();
});

onBeforeUnmount(() => {
    clearTimeout(data.checkCodeTimeout);
    delete data.checkCodeTimeout;

    clearTimeout(data.updateCodeTimeout);
    delete data.updateCodeTimeout;
});
</script>

<style lang="scss" scoped>
    .login {
        width: 36vw;
        padding: 72px 24px 24px;
        backdrop-filter: blur(10px) brightness(0.7);
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        min-width: 380px;

        h1 {
            margin: 0;
            font: var(--font-h3);
            font-weight: bold;   
            color: var(--white-500);
        }

        .description {
            display: flex;
            flex-direction: column;
            gap: 16px;
            margin: auto 0;
            p {
                margin: 0;
                padding: 0;
                font: var(--font-body-1);
                color: var(--primary-100);
                b {
                    color: var(--white-500);
                }
            }
            ul {
                display: flex;
                flex-direction: column;
                gap: 16px;
                list-style: none;
                margin: 0;
                padding: 0;
                li {
                    font: var(--font-body-2);
                    color: var(--white-500);
                }
            }
            .code {
                font: var(--font-h2);
                color: var(--white-500);
            }
        }

        .footer {
            margin-top: auto;
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 16px;
            justify-content: space-between;

            .terms {
                display: inline;
                span {
                    font: var(--font-caption);
                    color: var(--white-500);
                    display: flex;
                    flex-direction: column;
                    gap: 0;
                    align-items: start;
                    a {
                        font: var(--font-caption);
                        color: var(--primary-500);
                        transition: all .4s;
                        &:hover {
                            color: var(--primary-400);
                        }
                    }
                }
            }
        }
    }

    .direction-rtl .login {
        left: auto;
        right: 0;
        .footer {

        }
    }
</style>
